import React from "react";
import {inject, observer} from "mobx-react/index";

@inject('productStore')
@observer
export default class ProductsList extends React.Component {
  constructor(props) {
    super(props);
    this.loadAll();
  }

  render() {
    const {props: {productStore: {products}}} = this;
    return (
      <div>
        {products.map(({name, id, price, weight}) => (<div><h1>{name}</h1><br/><h2>{price}</h2><br/><h3>{weight}</h3>
          <button onClick={() => this.delete(id)}>{weight}</button>
        </div>))}
      </div>
    );
  }

  loadAll() {
    this.props.productStore.loadAll();
  }

  delete(id) {
    this.props.productStore.delete(id);
  }
}



